import { Grid, Typography } from "@mui/material";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";

const MainPage = observer(() => {
    return (
        <Grid container>
            <Grid item xs={8} container justifyContent={'start'}>
                <Typography variant="h4" align="start">
                          !
                </Typography>
                <br />
                <Typography variant="text16" align="start">
                                .
                        <Link to='/leaders/ranked'> </Link>.       
                             (   ,  
                         ).
                </Typography>
            </Grid>
            <Grid item xs={12} container justifyContent={'start'} alignContent={'start'}>
                <ul>
                    <li><Link to='/leaders/draft'> </Link></li>
                    <li><Link to='/leaders/list'> </Link></li>
                    <li><Link to='/leaders/rank'> </Link></li>
                    <li><Link to='/civs/list'> </Link></li>
                </ul>
            </Grid>
        </Grid>
    )
});

export default MainPage;
